/*------------------------------ <PARALLAX> ------------------------------*/
.parallax {
  flex: 1 0 100%;
  position: relative;
  overflow: hidden;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    background: linear-gradient(rgba(0,0,0,0,) 0%, rgba(0,0,0,1) 60%);
  }

  &__list {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    li {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }

  &__bg {
    position: absolute;
    width: 110%;
    height: 110%;
    top: -5%;
    left: -5%;
    background: url("../img/background.jpg") 50% 100% / cover no-repeat;
  }

  &__rope {
    position: absolute;
    width: 120%;
    left: -10%;
    opacity: 0;

    img {
      width: 100%;
    }

    &_1 {
      top: 5%;
      transform: scale(1);

      .parallax__element {
        transform: scale(0.6);
      }
    }

    &_2 {
      top: -1.66667%;
      transform: scale(1.05556);

      .parallax__element {
        transform: scale(0.75556);
      }
    }

    &_3 {
      top: -11.66667%;
      transform: scale(1.13889);

      .parallax__element {
        transform: scale(0.98889);
      }
    }
  }

  &__element {
    position: absolute;

    span {
      display: inline-block;
      transform-origin: 50% 0;
      width: 280px;
      height: 280px;
      margin: 0 0 0 -140px;
      animation: swing 2s infinite alternate cubic-bezier(0.455, 0.03, 0.515, 0.955);
    }

    &_1 {
      top: 28%;
      left: 20%;

      span {
        background: url("../img/clouds/board-cloud-2.png") 0 0 / 100% no-repeat;
      }
    }

    &_2 {
      top: 46%;
      left: 40%;

      span {
        background: url("../img/clouds/board-cloud-1.png") 0 0 / 100% no-repeat;
      }
    }

    &_3 {
      top: 24%;
      left: 80%;

      span {
        background: url("../img/clouds/board-birds.png") 0 0 / 100% no-repeat;
      }
    }

    &_4 {
      top: 10%;
      left: 17%;

      span {
        background: url("../img/clouds/board-cloud-1.png") 0 0 / 100% no-repeat;
      }
    }

    &_5 {
      top: 56%;
      left: 50%;

      span {
        background: url("../img/clouds/board-cloud-4.png") 0 0 / 100% no-repeat;
      }
    }

    &_6 {
      top: 57%;
      left: 30%;

      span {
        background: url("../img/clouds/board-birds.png") 0 0 / 100% no-repeat;
      }
    }

    &_7 {
      top: 65%;
      left: 60%;

      span {
        background: url("../img/clouds/board-cloud-2.png") 0 0 / 100% no-repeat;
      }
    }

    &_8 {
      top: 45%;
      left: 80%;

      span {
        background: url("../img/clouds/board-cloud-3.png") 0 0 / 100% no-repeat;
      }
    }
  }

  &__wave {
    position: absolute;
    background: url("../img/waves/wave-plain.png") 0 0 / auto 101% repeat-x;
    left: -100%;
    width: 300%;
    opacity: 0;

    &_1 {
      background: url("../img/waves/wave-paint.png") 0 0 / auto 101% repeat-x;
      height: 86px;
      bottom: 180px;
      animation: wave 7.11111s 0.1s infinite linear;
    }

    &_2 {
      height: 115px;
      bottom: 144px;
      animation: wave 6.66667s 0.1s infinite linear;
    }

    &_3 {
      background: url("../img/waves/wave-paint.png") 0 0 / auto 101% repeat-x;
      height: 144px;
      bottom: 108px;
      animation: wave 6.22222s 0.1s infinite linear;
    }

    &_4 {
      height: 173px;
      bottom: 72px;
      animation: wave 5.77778s 0.1s infinite linear;
    }

    &_5 {
      height: 230px;
      bottom: -1px;
      animation: wave 4.8888s 0.1s infinite linear;
    }

    &_6 {
      background: url("../img/waves/wave-paint.png") 0 0 / auto 101% repeat-x;
      height: 288px;
      bottom: -72px;
      animation: wave 4.5s 0.1s infinite linear;
    }
  }

  &__lighthouse {
    background: url("../img/lighthouse.png") 0 0 no-repeat;
    width: 320px;
    height: 560px;
    position: absolute;
    right: 12%;
    bottom: -100%;
    animation: lighthouse 4s 0.1s infinite alternate cubic-bezier(0.455, 0.03, 0.515, 0.955);
  }

  @keyframes swing {
    0% {
      transform: rotateZ(10deg);
    }

    100% {
      transform: rotateZ(-10deg);
    }
  }

  @keyframes wave {
    0% {
      transform: rotateZ(0deg) translate3d(0,10%,0) rotateZ(0deg);
    }

    100% {
      transform: rotateZ(360deg) translate3d(0,10%,0) rotateZ(-360deg);
    }
  }

  @keyframes lighthouse {
    0% {
      transform: translate3d(15%,0,0) rotateZ(10deg);
    }

    100% {
      transform: translate3d(-15%,0,0) rotateZ(-10deg);
    }
  }
}
/*------------------------------ </PARALLAX> -----------------------------*/